body {
  margin: 0 auto;
}
.body__popup {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: 2s;
  overflow: hidden;
}
.body__popup__card {
  overflow: hidden;
}

.header {
  width: 100%;
  top: 61px;
  padding-bottom: 21px;
  position: fixed;
  top: 0;
  background-color: white;
  z-index: 2;
}
.header .wrapper {
  display: flex;
  margin: 0 auto;
  max-width: 1200px;
  justify-content: space-between;
}
.header .wrapper .logo {
  display: flex;
  flex-direction: column;
  margin-right: auto;
}
.header .wrapper .logo:hover {
  cursor: pointer;
  opacity: 0.5;
}
.header .wrapper .logo .logo__title {
  color: #545454;
  font-family: "Georgia";
  font-size: 32px;
  letter-spacing: 2px;
  line-height: 110%;
  font-weight: normal;
  margin-top: 31px;
  margin-bottom: 0px;
  width: 200px;
}
.header .wrapper .logo .logo__title a {
  color: inherit;
  text-decoration: none;
}
.header .wrapper .logo .logo__subtitle {
  font-size: 13px;
  color: #545454;
  letter-spacing: 1.29px;
  font-family: "Arial";
  text-align: center;
  line-height: 33px;
  width: 187px;
}
.header .wrapper .logo .logo__subtitle a {
  color: inherit;
  text-decoration: none;
  padding-top: 30px;
}
.header .wrapper .menu {
  width: 443px;
  display: flex;
}
.header .wrapper .menu .logo__burger {
  display: none;
}
.header .wrapper .menu .logo__burger .logo__title a {
  text-decoration: none;
  color: inherit;
}
.header .wrapper .menu .logo__burger .logo__subtitle a {
  text-decoration: none;
  color: inherit;
}
.header .wrapper .menu .list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  margin-bottom: 12px;
  padding-left: 0;
  padding-top: 13px;
}
.header .wrapper .menu .list .list__item {
  display: flex;
  align-items: center;
}
.header .wrapper .menu .list .list__item .list__link {
  font-family: "Arial";
  font-size: 15px;
  line-height: 160%;
  text-decoration: none;
  color: #545454;
}
.header .wrapper .menu .list .list__item .list__link:hover {
  color: #292929;
  transition: 0.1s;
}
.header .wrapper .menu .list .list__item .list__link--active {
  color: #292929;
  border-bottom: solid 3px #F1CDB3;
}
.header .wrapper .menu .list .list__item .list__link--disabled {
  color: #545454;
  cursor: context-menu;
}
.header .wrapper .menu .list .list__item .list__link--disabled:hover {
  color: #545454;
  transition: 0;
}
.header .wrapper .toggle {
  display: none;
}
@media (min-width: 768px) and (max-width: 1279px) {
  .header .wrapper {
    margin-left: 4%;
    margin-right: 4%;
  }
  .header .wrapper .logo {
    margin-right: 77px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .header {
    left: 0px;
    min-width: 320px;
  }
  .header .wrapper #popup {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
    transition: 2s;
  }
  .header .wrapper .popup {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(41, 41, 41, 0.6);
    transition: 2s;
  }
  .header .wrapper .logo {
    padding-left: 10px;
  }
  .header .wrapper .logo .logo__title:hover {
    cursor: pointer;
    opacity: 0.5;
  }
  .header .wrapper .logo .logo__subtitle {
    line-height: 34px;
  }
  .header .wrapper .logo .display-none {
    display: none;
  }
  .header .wrapper .menu {
    position: fixed;
    max-width: 320px;
    top: -31px;
    padding-bottom: 195px;
    background: #f6f6f6;
    padding-top: 31px;
    flex-direction: column;
    right: -320px;
    transition: 1s;
    padding-top: 62px;
    opacity: 0;
    z-index: 2;
  }
  .header .wrapper .menu .logo__burger {
    display: block;
    margin-bottom: 120px;
    margin-left: 10px;
  }
  .header .wrapper .menu .logo__burger:hover {
    cursor: pointer;
    opacity: 0.5;
  }
  .header .wrapper .menu .logo__burger .logo__title {
    color: #F1CDB3;
    font-family: "Georgia";
    font-size: 32px;
    letter-spacing: 2px;
    line-height: 110%;
    font-weight: normal;
    margin: 0;
  }
  .header .wrapper .menu .logo__burger .logo__subtitle {
    font-size: 13px;
    color: black;
    letter-spacing: 1.29px;
    font-family: "Arial";
    line-height: 33px;
    padding-left: 4px;
  }
  .header .wrapper .menu .logo__burger .logo__subtitle a {
    padding-top: 30px;
  }
  .header .wrapper .menu .list {
    flex-direction: column;
    align-items: center;
  }
  .header .wrapper .menu .list .list__item {
    padding-bottom: 40px;
  }
  .header .wrapper .menu .list .list__item .list__link {
    font-size: 32px;
  }
  .header .wrapper .menu__active {
    right: 0;
    position: fixed;
    transition: 1s;
    z-index: 2;
    padding-top: 62px;
    opacity: 1;
    height: 100%;
  }
  .header .wrapper .toggle {
    position: absolute;
    right: 33px;
    display: block;
    width: 70px;
    height: 50px;
    cursor: pointer;
    border: none;
    background-color: transparent;
    z-index: 2;
    transition: 0.5s;
    margin-top: 35px;
    outline: none;
  }
  .header .wrapper .toggle::before {
    content: "";
    position: absolute;
    top: 14px;
    right: 20px;
    width: 30px;
    height: 2px;
    background-color: black;
    box-shadow: 0 10px 0 0 black, 0 20px 0 0 black;
  }
  .header .wrapper .toggle__active {
    transition: 0.5s;
    transform: rotate(90deg);
    position: fixed;
  }
}

.our-friend {
  padding-top: 46px;
  padding-bottom: 100px;
  background: #F6F6F6;
  margin-top: 120px;
}
.our-friend .our-friend__wrapper {
  max-width: 1200px;
  margin: 0 auto;
}
.our-friend .our-friend__wrapper .our-friend__content .our-friend__title {
  text-align: center;
  font-family: "Georgia";
  font-size: 35px;
  font-weight: normal;
  color: #545454;
  line-height: 130%;
  letter-spacing: 2.1px;
}
.our-friend .our-friend__wrapper .our-friend__slider {
  display: flex;
  justify-content: space-between;
  margin-top: 59px;
  flex-wrap: wrap;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__early {
  width: 52px;
  height: 52px;
  background: url(../../assets/icons/arrow-left.svg) #fff;
  border: 2px solid #f1cdb3;
  border-radius: 100px;
  margin-top: 192px;
  background-repeat: no-repeat;
  background-position-x: 17px;
  background-position-y: 21px;
  background-color: transparent;
  transition: 0.3s;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__early:hover {
  transition: 0.3s;
  cursor: pointer;
  background-color: #FDDCC4;
  border: 2px solid #FDDCC4;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__early--inactive {
  background-image: url(../../assets/icons/arrow-left__inactive.svg);
  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__early--inactive:hover {
  cursor: auto;
  background-color: transparent;
  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next {
  width: 52px;
  height: 52px;
  background: url(../../assets/icons/arrow-right.svg) #fff;
  border: 2px solid #f1cdb3;
  border-radius: 100px;
  margin-top: 192px;
  background-repeat: no-repeat;
  background-position-x: 17px;
  background-position-y: 21px;
  background-color: transparent;
  transition: 0.3s;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next:hover {
  transition: 0.3s;
  cursor: pointer;
  background-color: #FDDCC4;
  border: 2px solid #FDDCC4;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next--inactive {
  background-image: url(../../assets/icons/arrow-right__inactive.svg);
  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next--inactive:hover {
  cursor: auto;
  background-color: transparent;
  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card:hover .our-friend__button {
  transition: 0.3s;
  background-color: #fddcc4;
  border: 2px solid #fddcc4;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card {
  background: black;
  padding-bottom: 30px;
  border-radius: 10px;
  background: #FAFAFA;
  transition: 0.3s;
  margin-bottom: 30px;
  cursor: pointer;
  position: relative;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card:hover {
  transition: 0.3s;
  background-color: white;
  box-shadow: 0 1px 20px rgba(0, 0, 0, 0.05), 0 10px 10px rgba(0, 0, 0, 0.02);
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .our-friend__name {
  font-family: "Georgia";
  color: #545454;
  font-size: 20px;
  text-align: center;
  margin-top: 26px;
  letter-spacing: 1px;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .fullScreen {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .our-friend__button {
  width: 188px;
  border-radius: 100px;
  background-color: transparent;
  padding: 13px;
  margin-top: 11px;
  font-size: 17px;
  line-height: 130%;
  font-family: "Georgia";
  color: #292929;
  cursor: pointer;
  margin-left: 41px;
  letter-spacing: 1px;
  border: 2px solid #f1cdb3;
  transition: 0.3s;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .our-friend__button:hover {
  transition: 0.3s;
  background-color: #fddcc4;
  border: 2px solid #fddcc4;
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container {
