  animation-name: popupClose;
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers {
  width: 900px;
  margin: 0 auto;
  position: relative;
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .pets__popup {
  position: absolute;
