}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .pets__popup .pets__wrapper {
  width: 900px;
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .pets__popup .pets__name {
  text-align: left;
  font-size: 35px;
  font-weight: 400;
