}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .close {
  position: absolute;
  top: -20px;
  width: 50px;
  height: 50px;
  right: -56px;
  border-radius: 50px;
  background: transparent;
  border: 2px solid #F1CDB3;
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .close:hover {
