  opacity: 0.6;
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .close:before,
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .close:after {
  position: absolute;
  content: ' ';
  top: 18px;
  left: 22px;
  height: 12px;
  width: 2px;
  background-color: #333;
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .close:before {
  transform: rotate(45deg);
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container .full__wrappers .close:after {
  transform: rotate(-45deg);
}
.our-friend .our-friend__wrapper .our-friend__slider .pets__container__active {
  top: 25%;
  width: 100%;
  height: 100%;
  transition: 2s;
  position: fixed;
  left: 0;
  overflow: inherit;
  animation-duration: 2s;
  animation-name: popupClose;
  margin: 0 auto;
  z-index: 3;
}
.our-friend .our-friend__wrapper .our-friend__slider .popup__card {
  position: fixed;
  left: 0;
  top: 0;
